import React, { Component } from 'react';
import { createStore} from 'redux'
import { Provider, connect } from 'react-redux'
import './App.css';
import question from "./Questions"
import Quizz from "./Quizz";
const QUESTION = "QUESTION";
const getQuestion = ()=>({type: QUESTION})
const questionReducer = (state = null,action)=>{
  switch(action.type){
    case QUESTION:
      return question();
    default:
      return question()
  }
}

const store = createStore(questionReducer)

const mapStateToProps = (state)=>({questions: state});
const mapDispatchToProps = (dispatch)=>({newQuestions: ()=>(dispatch(getQuestion()))})
const Container = connect(mapStateToProps,mapDispatchToProps)(Quizz)
class App extends Component{
  render(){
    return (
      <Provider store = {store}>
        <Container />
      </Provider>
    )
  }
}

export default App;