import React, {Component} from "react"
import color from "./Color"
class Quizz extends Component{
    constructor(props){
      super(props);
      this.state = {
        "q1a": this.props.questions[0].answers,
        "q2a": this.props.questions[1].answers,
        "q3a": this.props.questions[2].answers,
        "q4a": this.props.questions[3].answers,
        "q5a": this.props.questions[4].answers,
        "answered": [],
        correct: [],
        style: {display: "flex"},
        resultStyle: {display: "none"},
        submitStyle: "initial",
        warningStyle: "none",
        color: color()
      }
      this.q1 = this.q1.bind(this)
      this.q2 = this.q2.bind(this)
      this.q3 = this.q3.bind(this)
      this.q4 = this.q4.bind(this)
      this.q5 = this.q5.bind(this)
      this.handleSubmit = this.handleSubmit.bind(this);
      this.playAgain = this.playAgain.bind(this)
    }

    q1(ans){
      this.setState({q1a: [ans],answered:[...this.state.answered,1]});
      if(this.props.questions[0].correct==ans){
        this.setState({correct:[...this.state.correct,1]})
      }
      if(this.state.answered.includes(1)){
        this.setState({warningStyle:"initial"})
      }else{this.setState({warningStyle:"none"})}
    }
    q2(ans){
      this.setState({q2a: [ans],answered:[...this.state.answered,2]});
      if(this.props.questions[1].correct==ans){
        this.setState({correct:[...this.state.correct,2]})
      }
      if(this.state.answered.includes(2)){
        this.setState({warningStyle:"initial"})
      }else{this.setState({warningStyle:"none"})}
    }
    q3(ans){
      this.setState({q3a: [ans],answered:[...this.state.answered,3]});
      if(this.props.questions[2].correct==ans){
        this.setState({correct:[...this.state.correct,3]})
      }
      if(this.state.answered.includes(3)){
        this.setState({warningStyle:"initial"})
      }else{this.setState({warningStyle:"none"})}
    }
    q4(ans){
      this.setState({q4a: [ans],answered:[...this.state.answered,4]});
      if(this.props.questions[3].correct==ans){
        this.setState({correct:[...this.state.correct,4]})
      }
      if(this.state.answered.includes(4)){
        this.setState({warningStyle:"initial"})
      }else{this.setState({warningStyle:"none"})}
    }
    q5(ans){
      this.setState({q5a: [ans],answered:[...this.state.answered,5]});
      if(this.props.questions[4].correct==ans){
        this.setState({correct:[...this.state.correct,5]})
      }
      if(this.state.answered.includes(5)){
        this.setState({warningStyle:"initial"})
      }else{this.setState({warningStyle:"none"})}
    }

    handleSubmit(){
      let totalAnswers = []
      for(let i in this.state.answered){
        if(totalAnswers.indexOf(this.state.answered[i])==-1){
          totalAnswers.push(this.state.answered[i]);
        }
      }
      if(totalAnswers.length<5){this.setState({warningStyle: "initial"});return}
      let totalCorrect = [];
      for(let i in this.state.correct){
        if(totalAnswers.indexOf(this.state.correct[i]==-1)){
          totalCorrect.push(this.state.correct)
        }
      }
      this.setState({style: {display : "none"},resultStyle: {display: "flex"},correct: totalCorrect})
      this.props.newQuestions();
    }
    playAgain(){
      this.setState({correct:[],answered:[],style:{display: "flex"},resultStyle: {display: "none"}});
      this.setState({q1a: this.props.questions[0].answers,
        q2a: this.props.questions[1].answers,
        q3a: this.props.questions[2].answers,
        q4a: this.props.questions[3].answers,
        q5a: this.props.questions[4].answers,
        color: color()})
    }
    render(){
      return (
        <div className = "appDiv" style = {{backgroundColor: this.state.color}}>
            <div className = "container" style = {this.state.style}>
              <h1 style = {{color: this.state.color}}>Quick Quizzz...</h1>
              <div className = "qdiv">
                  <div className = "qnaDiv">
                  <div className = "question"><p id = "question" style = {{color: this.state.color}}>{this.props.questions[0].question}</p></div>
                  <div className = "options">
                    {this.state.q1a.map(a=><button onClick = {()=>this.q1(a)} key = {a} style = {{backgroundColor: this.state.color}} className = "btn">{a}</button>)}
                  </div>
                  </div>
                  
                  <div className = "qnaDiv">
                  <div className = "question"><p id = "question" style = {{color: this.state.color}}>{this.props.questions[1].question}</p></div>
                  <div className = "options">
                    {this.state.q2a.map(a=><button onClick = {()=>this.q2(a)} key = {a} style = {{backgroundColor: this.state.color}} className = "btn">{a}</button>)}
                  </div>
                  </div>
                  <div className = "qnaDiv">
                  <div className = "question"><p id = "question" style = {{color: this.state.color}}>{this.props.questions[2].question}</p></div>
                  <div className = "options">
                    {this.state.q3a.map(a=><button onClick = {()=>this.q3(a)} key = {a} style = {{backgroundColor: this.state.color}} className = "btn">{a}</button>)}
                  </div>
                  </div>
                  <div className = "qnaDiv">
                  <div className = "question"><p id = "question" style = {{color: this.state.color}}>{this.props.questions[3].question}</p></div>
                  <div className = "options">
                    {this.state.q4a.map(a=><button onClick = {()=>this.q4(a)} key = {a} style = {{backgroundColor: this.state.color}} className = "btn">{a}</button>)}
                  </div>
                  </div>
                  <div className = "qnaDiv">
                  <div className = "question"><p id = "question" style = {{color: this.state.color}}>{this.props.questions[4].question}</p></div>
                  <div className = "options">
                    {this.state.q5a.map(a=><button onClick = {()=>this.q5(a)} key = {a} style = {{backgroundColor: this.state.color}} className = "btn">{a}</button>)}
                  </div>
                  </div>
              </div>
              <p className = "warning" style = {{color: "#ff726f",marginTop: 10,display: this.state.warningStyle}}>Please Choose All Answers</p>
              <button className = "submit" style = {{marginTop: "1rem", display: this.state.submitStyle, color: "white", backgroundColor: this.state.color}} onClick = {this.handleSubmit}>CHECK</button>
              <h3 style = {{color: this.state.color}}>Created By Ozair</h3>
          </div>
          <div className = "resultDiv" style = {this.state.resultStyle}>
            <div className = "resultItems">
              <p className = "result">You Have Got {this.state.correct.length}/5 Correct Answers</p>
              <button className = "playAgain" onClick = {this.playAgain}><i className = "fas fa-redo-alt"></i> Play Again</button>
            </div>
          </div>
        </div>
      )
    }
  }
export default Quizz